@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');

body {
  font-family: 'Poppins', sans-serif;
  background-color: #f4f4f4;
  text-align: center;
  color: #333333;
}

h1 {
  margin-top: 10px;
  font-weight: bold; 
  font-family: 'Poppins', sans-serif;
  color: #3a7ca5;
  font-size: 28px;
  margin-bottom: 20px;
}

.species-table {
  width: 80%;
  margin: auto;
  border-collapse: collapse;
  background-color: #eaf5f7;  
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

th, td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: center;
}

th {
  background-color: #d9f1f6;
}

.species-img {
  width: 150px ; 
  height: auto;
  border-radius: 20px ;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
  display: block;
  margin: auto;
}

.image-credit {
  font-size: 12px;
  color: #333333;
}

.species-table ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.species-table li {
  font-size: 14px;
  padding: 3px;
}

.species-table td ul {
  list-style-type: disc; 
  padding-left: 40px; 
  margin-top: 5px; 
}

.species-table td ul li {
  text-align: left; 
  font-size: 14px;
}

.bionano-highlight {
  position: relative;
}

.bionano-highlight td:first-child::before {
  content: "Bionano";
  display: block;
  position: absolute;
  top: -2px;
  left: -10px;
  background: red;
  color: white;
  padding: 5px 10px;
  font-size: 15px;
  font-weight: bold;
  border-radius: 3px;
  z-index: 10;
}
footer {
  margin-top: auto; 
  padding: 20px;
  background: #333;
  color: #eaf5f7;
  text-align: center;
  position: absolute; 
  bottom: 0;
  width: 100%;
}

.species-container {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 50px;
  max-width: 1600px;
  margin: auto;
   padding: 40px;
}

.species-sidebar {
  width: 200px;
  background: #f0f8ff;
  border-radius: 10px;
  padding: 15px;
  position: fixed;
  font-size: 16px; 
  top: 20px;
  left: 20px;
}

.species-sidebar h2 {
  text-align: center;
  font-size: 18px;
  font-weight: bold;
}

.species-sidebar ul {
  list-style: none;
  padding: 0;
}

.species-sidebar li {
  margin-bottom: 10px;
}

.species-sidebar a {
  display: block;
  padding: 10px;
  background: #dbeeff;
  text-decoration: none;
  color: black;
  border-radius: 5px;
  transition: background 0.3s;
  text-align: center;
}

.species-sidebar a:hover {
  background: #b8d8ff;
}

.species-content {
  width: 80%;
  max-width: 1000px;
  position: relative;
  padding: 30px;
}

.species-content ul {
  list-style-position: inside;
  padding-left: 0;
}

.species-content li {
  text-align: left; 
  padding-left: 10px;
}

.species-image-container {
  position: absolute;
  top: 20px;
  right: 28px;
  text-align: center;
    z-index: 10;
  width: 250px; 
}

.species-image {
  width: 100%;
  border-radius: 15px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
  display: block;
  margin: auto;
}

.image-credit {
  font-size: 12px;
  color: gray;
  margin-top: 5px;
}

h1 {
  text-align: center;
  font-size: 28px;
  margin-bottom: 20px;
}

h2 {
  text-align: center;
  font-size: 22px;
  position: relative;
  margin: 40px 0;
}

.resource-table {
  width: 100%;
  border-collapse: collapse;
}

.resource-table th, .resource-table td {
  padding: 12px;
  border: 1px solid #ccc;
  text-align: center;
}

.resource-table tr.even {
  background: #e0e0e0;
}

.resource-table tr.odd {
  background: transparent;
}

.social-links {
  position: fixed;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
}

.social-links a {
  display: block;
  margin-bottom: 15px;
  font-size: 40px; 
  text-decoration: none;
}

h2 {
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  position: relative;
  margin: 40px 0;
  display: flex;
  align-items: center; 
  justify-content: center;
}

h2::before,
h2::after {
  content: "";
  flex: 1;
  height: 2px;
  background: #74aee9;
  margin: 0 15px;
}

